/**
  * ===========================================================
  * LOCATION mobile
  * ===========================================================
  */
.location {
  height: 350px;
  position: relative;
}

.location__map-wrap {
  position: absolute;
  bottom: -5px;
  left: 0;

  max-width: 100%;
  height: auto;
}

.location__map {
  padding: 0;
  max-width: 1200px;
  height: auto;

  z-index: 0;
}

.location__map-frame {
  position: absolute;

  top: 0;
  left: 50%;

  margin-left: -601px;

  height: 350px;
  width: 1200px;

  border: 0;

  z-index: 1000;


  &--nojs {
    display: none;
  }
}

.location__mark {
  .absolute-top-left(161px, 146px, 27px, 27px);

  z-index: 100;
}

/**
  * ===========================================================
  * LOCATION tablet
  * ===========================================================
*/

@media (min-width: @tablet-max-width) {
  .location__mark {
    .absolute-top-left(163px, 370px, 27px, 27px);
    z-index: 100;
  }
}

/**
  * ===========================================================
  * LOCATION desktop
  * ===========================================================
*/

@media (min-width: @desktop-max-width) {
  .location {
    width: 1200px;
    margin: 0 auto;
  }

  .location__mark {
    .absolute-top-left(162px, 586px, 27px, 27px);
    z-index: 100;
  }
}
